@import url(https://fonts.googleapis.com/css?family=Amatic+SC:400,700&display=swap&subset=latin-ext);
*{margin:0;padding:0;box-sizing:border-box}

body{background-image:url(/weather-app/static/media/background-image.1828d026.png);background-size:cover;font-family:'Amatic SC', sans-serif}body .container{width:100vw;height:100vh;display:flex;flex-direction:column;align-items:center}body .container header{display:flex;align-items:center}body .container header h1{color:darkslategray;font-size:5rem;text-align:center}@media (min-width: 450px){body .container header h1{font-size:6rem}}body .container footer{margin-top:auto;display:flex;color:white;font-size:2rem;opacity:0.8}body .container footer a{display:flex;text-decoration:none;color:white;opacity:0.8}body .container footer .li-icon{margin:auto 1rem}body .container footer .li-icon:hover{color:lightgreen;-webkit-transition:0.1s ease-in-out;transition:0.1s ease-in-out}body .container footer .git-icon{margin:auto 0}body .container footer .git-icon:hover{color:lightgreen;-webkit-transition:0.1s ease-in-out;transition:0.1s ease-in-out}

.weather-container{margin-top:0.5rem;justify-content:space-evenly;display:flex;flex-direction:column;align-items:center;font-size:2rem;border-radius:2rem;background-color:#569358;opacity:0.9}.weather-container .weather-date{font-size:1.6rem;font-weight:700}.weather-container .weather-temp{font-weight:700;background-color:#397A4C;padding:0.5rem;border-radius:1rem}.weather-container .temperature{font-size:1.5rem}.weather-container .weather-temp__minmax{display:flex;justify-content:space-evenly;align-items:center;font-weight:700;width:100%}.weather-container .weather-temp__minmax .arrow-down{font-size:1.5rem}.weather-container .weather-temp__minmax .arrow-up{font-size:1.5rem}.loading{margin-top:2rem;display:flex;flex-direction:column;align-items:center;font-size:2rem;border:none;border-radius:2rem;background-color:#569358}

form{display:flex;flex-direction:column;align-items:center}form input{margin:0.3rem;border:0.1rem solid transparent;border-radius:3rem;outline:none;width:70%;padding:0.5rem;color:darkslategrey;text-align:center;font-family:'Amatic SC', sans-serif;font-size:2rem;cursor:pointer}form input:focus{border:0.1rem solid #397A4C}form input::-webkit-input-placeholder{color:darkslategrey}form input:-ms-input-placeholder{color:darkslategrey}form input::-ms-input-placeholder{color:darkslategrey}form input::placeholder{color:darkslategrey}form button{width:50%;border:none;border-radius:3rem;padding:0.5rem;margin:0.2rem;font-size:2rem;font-weight:700;background-color:#397A4C;font-family:'Amatic SC', sans-serif;color:white;outline:none;cursor:pointer}form button:hover{opacity:0.9;-webkit-transition:0.2s ease-in-out;transition:0.2s ease-in-out}

